@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff2") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 280px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1220px;
  }
}

.button {
  padding: 10px 12px;
  display: inline-block;
  border: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
}
.button--secondary {
  background-color: #f2f2f2;
  color: #444444;
}

.page {
  height: 100%;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__body {
  margin: 0;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.page__main {
  flex-grow: 1;
}

@media (min-width: 768px) {
  .page-header__nav {
    width: 708px;
    margin: 0 auto;
    padding: 24px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 1280px) {
  .page-header__nav {
    width: 1220px;
  }
}

@media (max-width: 767px) {
  .page-header__menu {
    background-color: #ffffff;
  }
  .page-header__nav--closed .page-header__menu {
    display: none;
  }
  .page-header__nav--opened .page-header__menu {
    position: absolute;
    right: 0;
    left: 0;
    display: block;
  }
  .page-header__nav--nojs .page-header__menu {
    position: static;
    min-height: 0;
  }
}

@media (max-width: 767px) {
  .page-header__bar {
    margin: 0 auto;
    padding: 12px 20px;
    width: 320px;
    display: grid;
    grid-template-columns: 34px 1fr 34px;
    gap: 16px;
    justify-content: space-between;
    align-items: center;
  }
}

.page-header__logo {
  grid-column: 1/3;
}

.page-header__toggle {
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-image: url(../../img/icons/burger.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__menu {
  margin: 0;
}
@media (min-width: 768px) {
  .page-header__menu {
    margin-right: -17px;
  }
}

.promo {
  padding-top: 27px;
  color: #000000;
}
@media (max-width: 767px) {
  .promo {
    text-align: center;
    color: #ffffff;
    background-color: #68b738;
    background-image: url(../../img/background/promo-small.jpg), linear-gradient(#63a63b 0, #63a63b 302px, #ffffff 302px);
    background-repeat: no-repeat;
    background-position: 50% 0;
  }
}
@media (min-width: 768px) {
  .promo {
    background-color: transparent;
  }
}
@media (min-width: 1280px) {
  .promo {
    position: relative;
  }
  .promo::after {
    content: "";
    position: absolute;
    top: -200px;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: -2;
    background-color: #63a63b;
    background-image: url("../../img/background/promo-large.jpg");
    background-repeat: no-repeat;
    background-position: 0 100%;
  }
}

@media (min-width: 768px) {
  .promo__container {
    position: relative;
    padding: 0 90px 526px;
  }
}
@media (min-width: 1280px) {
  .promo__container {
    min-height: 582px;
    padding: 20px 610px 95px 80px;
  }
}

.promo__title {
  margin: 0;
  margin-bottom: 25px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 1;
  font-weight: 400;
}
@media (min-width: 768px) {
  .promo__title {
    margin-bottom: 41px;
    font-size: 60px;
  }
}

.promo__subtitle {
  margin: 0;
  margin-bottom: 29px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__subtitle {
    margin-bottom: 49px;
    font-size: 20px;
  }
}

.promo__image {
  width: 280px;
}
@media (min-width: 768px) {
  .promo__image {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
    width: 709px;
    height: 609px;
  }
}
@media (min-width: 1280px) {
  .promo__image {
    top: 30px;
    left: calc(50% - 162px);
    width: 552px;
    height: 499px;
  }
}

.promo__button {
  width: 100%;
}
@media (min-width: 768px) {
  .promo__button {
    padding: 14px 26px;
    width: auto;
    font-size: 20px;
  }
}

.catalog-section {
  padding-top: 20px;
}
@media (min-width: 1280px) {
  .catalog-section {
    padding-top: 82px;
  }
}

@media (min-width: 1280px) {
  .catalog-section__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.catalog-section__item {
  position: relative;
  padding: 20px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .catalog-section__item {
    padding: 77px 316px 52px 52px;
  }
}
@media (min-width: 1280px) {
  .catalog-section__item {
    padding: 40px 50px 55px;
  }
}
.catalog-section__item + .catalog-section__item {
  margin-top: 20px;
}
@media (min-width: 768px) {
  .catalog-section__item + .catalog-section__item {
    margin-top: 30px;
  }
}
@media (min-width: 1280px) {
  .catalog-section__item + .catalog-section__item {
    margin-top: 0;
  }
}

.catalog-section__title {
  margin: 0;
  margin-bottom: 24px;
  padding-left: 70px;
  position: relative;
  display: flex;
  align-items: center;
  min-height: 50px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .catalog-section__title {
    position: static;
    padding-left: 0;
    font-size: 36px;
  }
}
@media (min-width: 1280px) {
  .catalog-section__title {
    position: relative;
    padding-left: 162px;
    min-height: 100PX;
  }
}
.catalog-section__title::before {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  left: 0;
  border-radius: 50%;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .catalog-section__title::before {
    top: 92px;
    right: 62px;
    left: auto;
    width: 200px;
    height: 200px;
  }
}
@media (min-width: 1280px) {
  .catalog-section__title::before {
    top: 0;
    left: 0;
    width: 100px;
    height: 100px;
  }
}
.catalog-section__title::after {
  content: "";
  position: absolute;
  background-size: contain;
}

.catalog-section__title--slim::after {
  top: 3px;
  left: 7px;
  width: 36px;
  height: 50px;
  background-image: url(../../img/icons/cat-slim.svg);
}
@media (min-width: 768px) {
  .catalog-section__title--slim::after {
    top: 80px;
    right: 92px;
    left: auto;
    width: 140px;
    height: 194px;
  }
}
@media (min-width: 1280px) {
  .catalog-section__title--slim::after {
    top: -6px;
    right: auto;
    left: 15px;
    width: 70px;
    height: 97px;
  }
}

.catalog-section__title--pro::after {
  top: 11px;
  left: -10px;
  width: 67px;
  height: 28px;
  background-image: url(../../img/icons/cat-pro.svg);
}
@media (min-width: 768px) {
  .catalog-section__title--pro::after {
    top: 135px;
    right: 28px;
    left: auto;
    width: 268px;
    height: 112px;
  }
}
@media (min-width: 1280px) {
  .catalog-section__title--pro::after {
    top: 20px;
    right: auto;
    left: -19px;
    width: 134px;
    height: 56px;
  }
}

.catalog-section__description {
  margin-bottom: 15px;
  padding-bottom: 23px;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .catalog-section__description {
    margin-bottom: 48px;
    border-bottom: none;
  }
}

.catalog-section__link {
  display: grid;
  grid-template-columns: max-content 32px;
  align-items: center;
  gap: 15px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .catalog-section__link {
    font-size: 20px;
  }
}
.catalog-section__link::after {
  content: "";
  width: 24px;
  height: 10px;
  background-repeat: no-repeat;
  background-position: 100% 50%;
  background-image: url(../../img/icons/arrow.svg);
}
.catalog-section__link:hover::after {
  width: 32px;
}

.process {
  padding: 20px;
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .process {
    padding: 50px 30px;
  }
}

.process__title {
  margin: 0;
  margin-bottom: 40px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  margin-bottom: 40px;
  color: #000000;
}
@media (min-width: 768px) {
  .process__title {
    font-size: 60px;
    line-height: 1;
  }
}

.process__list {
  list-style-type: none;
  padding: 0;
  counter-reset: process 0;
}
@media (min-width: 768px) {
  .process__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    row-gap: 36px;
  }
}
@media (min-width: 1280px) {
  .process__list {
    grid-template-columns: repeat(4, 1fr);
    gap: 80px;
  }
}

.process__item {
  position: relative;
  display: grid;
  grid-template-columns: 60px 1fr;
  gap: 20px;
  counter-increment: process 1;
}
@media (max-width: 767px) {
  .process__item br {
    display: none;
  }
}
@media (min-width: 768px) {
  .process__item {
    grid-template-columns: auto;
    grid-template-rows: 80px auto;
    gap: 30px;
    padding-top: 40px;
    min-height: 280px;
  }
}
@media (max-width: 767px) {
  .process__item + .process__item {
    margin-top: 20px;
  }
}
.process__item::before {
  content: "";
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 768px) {
  .process__item::before {
    width: 80px;
    height: 80px;
  }
}
.process__item--leaf::before {
  background-image: url(../../img/icons/leaf.svg);
}
.process__item--tub::before {
  background-image: url(../../img/icons/tub.svg);
}
.process__item--eat::before {
  background-image: url(../../img/icons/eat.svg);
}
.process__item--alarm::before {
  background-image: url(../../img/icons/alarm.svg);
}
@media (min-width: 768px) {
  .process__item::after {
    content: counter(process);
    position: absolute;
    top: 0;
    width: 136px;
    right: 75px;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 280px;
    line-height: 1;
    text-align: center;
    color: #f2f2f2;
    z-index: -1;
  }
}
@media (min-width: 1280px) {
  .process__item::after {
    right: 0;
  }
}

