  margin-bottom: 40px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  font-weight: 400;
}
@media (min-width: 768px) {
  .example__title {
    font-size: 64px;
    line-height: 1;
