.slider__toggler {
  padding: 4px 6px;
  display: flex;
  background-color: #ffffff;
  border: 1px solid #cdcdcd;
  border-radius: 6px;
}
@media (min-width: 768px) {
  .slider__toggler {
    display: none;
  }
}
.slider__toggler::after {
  content: "";
  width: 50%;
  height: 6px;
  background-color: #68b738;
  border-radius: 6px;
}
.slider--after .slider__toggler {
  justify-content: flex-end;
}
.slider--before .slider__toggler {
  justify-content: flex-start;
}

.slider__bar {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
@media (max-width: 767px) {
  .slider__bar {
    display: none;
  }
}

.slider__bar::-webkit-slider-runnable-track {
  height: 6px;
  border-radius: 6px;
  background-color: #dcdcdc;
}

.slider__bar::-webkit-slider-thumb {
  appearance: none;
  width: 32px;
  height: 32px;
  margin-top: -12px;
  border: 12px solid #ffffff;
  border-radius: 50%;
  background-color: #68b738;
  filter: drop-shadow(0px 1px 3px rgba(0, 0, 0, 0.16));
  cursor: pointer;
}

